.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}
.head-image{
    width: auto;
    transition: max-width 1s, min-width 1s;
}
.head-image-expanded{
    min-width: 230px;
    max-width: 250px;
}
.head-image-contrasted{
    min-width: 40px;
    max-width: 60px;
}
.head-text{
    font-family: 'Hachi maru pop',cursive;
    margin: 5px 0 15px 0;
    transition: font-size 1s;
}
.head-text-expanded{
    font-size: 36px;
}
.head-text-contrasted{
    font-size: 12px;
}
a{
    text-decoration: none;
}
.result-name-card{
    color: #e7e9f0;
    background:#282d4f ;
    margin: 10px 15px;
    min-width: 75px;
    padding: 10px 20px ;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(255, 255, 255, .1);
    transition: background .35s;
    display: flex;
    justify-content: center;
}
.result-name-card:hover{
    background: #4a5079;
    box-shadow: 0 0 5px rgba(255, 255, 255, .2);
}
.result-name{
    margin: 0;
    font-weight: 100;

}
.result-container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px 40px;
}
.search-container{
    display: flex;
    justify-content: center;
    margin: 10px;
}
.search-input{
    padding: 8px 15px;
    font-size: 25px;
    width: 250px;
    border-width: 0;
    background: #a592f2;
    color: #e7e9f0;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input::-webkit-input-placeholder{
    color: #e7e9f0;

}

.search-input::placeholder{
    color: #e7e9f0;

}
.search-input:focus{
    outline: none;
}
body{
    background: #0f1228;
    overflow-x: hidden;
    color: #e7e9e0;
    font-family: 'Lato',sans-serif;
}
